import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { useCustomTranslation } from '../../../../translation'
import { CustCheckbox, DynamicFilter, Label } from '../../../commanComponent'
import { CustomButton, CustomSpinner } from '../../../globalComponents'
import CustomSelect from '../../../commanComponent/CustomSelect'
import BackIcon from '../../../commanComponent/BackIcon'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { pagination } from '../../../../utils/Constants.config'
import { departmentAction, employeeActions, payrollActions } from '../../../../core/resource/actions'

const CreatePayrun = () => {
    const dispatch = useDispatch()
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    } = useSelector(state => state.department)
    const {
        getAllEmployeePending,
        getAllEmployeeRes,
        getAllEmployeeError,
    } = useSelector(state => state.employee)
    const {
        getEmployeeSalaryPending,
        getEmployeeSalaryRes,
        getEmployeeSalaryError,

        addPayRunPending,
        addPayRunRes,
        addPayRunError,

    } = useSelector(state => state.payroll)

    const { t } = useCustomTranslation()
    const [currentPage, setCurrentPage] = useState(1)
    const [createdSalaryEmpList, setCreatedSalaryEmpList] = useState('')
    const [allActiveSalaryCreate, setAllActiveSalaryCreate] = useState(true)
    const [tempSalaryEmpData, setTempSalaryEmpData] = useState('')
    const [allDepartments, setAllDepartments] = useState('')
    const [allEmployeeList, setAllEmployeeList] = useState('')
    const [filterFormData, setFilterFormData] = useState({
        employeeName: '',
        department: '',
    });

    const createPayRunFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'hold_salary', label: 'Hold Salary' },
        { key: 'create_salary', label: 'Create Salary' },
        { key: 'emp_code', label: 'Emp ID' },
        { key: 'employee_name', label: 'Emp Name' },
        { key: 'email', label: 'Email' },
        { key: 'department', label: 'Department  ' },
        { key: 'designation', label: 'Designation' },
    ];

    const createPayRunScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        hold_salary: (item) => (
            <span>
                <CustCheckbox
                    key={"is_hold"}
                    id={`is_hold`}
                    checked={item?.is_hold}
                    handleChange={(e) => handleCheckedUncheck(e, null, item)}
                    name={"is_hold"}
                />
            </span>
        ),
        create_salary: (item) => (
            <span>
                <CustCheckbox
                    key={"create_salary"}
                    id={`create_salary`}
                    checked={item?.create_salary}
                    handleChange={(e) => handleCheckedUncheck(e, null, item)}
                    name={"create_salary"}
                />
            </span>
        ),


    }), [allActiveSalaryCreate, createdSalaryEmpList]);

    useEffect(() => {
        getAllDepartments()
    }, [])

    const employeeFilters = [
        {
            name: 'employeeName',
            label: 'Select Employee / ID',
            type: 'select',
            value: filterFormData.employeeName,
            data: allEmployeeList,
            placeholder: 'Enter Employee Name',
            isMulti: true
        },
        {
            name: 'department',
            label: 'Department',
            type: 'select',
            data: allDepartments,
            value: filterFormData.department,
            placeholder: 'Select Department',
        },

    ];

    const handleFilterFormData = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setFilterFormData((prevState) => ({
            ...prevState,
            [name]: type === 'checkbox' ? checked : value,
        }));
    };

    const handleFilterEmployees = () => {
        const filterFields = {};
        if (filterFormData?.department?.value) filterFields["department_id"] = filterFormData?.department?.value;
        if (filterFormData?.designation?.value) filterFields["designation_id"] = filterFormData?.designation?.value;
        if (filterFormData?.employeeName) filterFields["employee_name"] = filterFormData?.employeeName
        console.log(filterFields, "filterFields---------")
        // searchFilterData(filterFields, tempSalaryEmpData?.data)
    }
    const handleCheckedUncheck = (e, fieldName, checkedEmp) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreatedSalaryEmpList((prevState) => {
            const updatedData = prevState?.data?.map((item) => {
                if (checkedEmp?._id === item?._id) {
                    return {
                        ...item,
                        [name]: type === 'checkbox' ? checked : value,
                    };
                }
                return item;
            });
            const allChecked = updatedData.every((item) => item.create_salary);
            setAllActiveSalaryCreate(allChecked);

            return {
                ...prevState,
                data: updatedData,
            };
        });
    };

    /* Get Employee Salary Data */

    useEffect(() => {
        getEmployeeSalary()
    }, [currentPage])

    const getEmployeeSalary = () => {
        // const payload = {
        //     page: currentPage,
        //     limit: 1,
        // }
        dispatch(payrollActions?.getEmployeeSalaryRequest())
    }

    useEffect(() => {
        if (!getEmployeeSalaryPending) {
            if (getEmployeeSalaryError) {
                return;
            }
            if (getEmployeeSalaryRes) {
                if (getEmployeeSalaryRes?.code === 200) {
                    setCreatedSalaryEmpList(getEmployeeSalaryRes?.data)
                    setTempSalaryEmpData(getEmployeeSalaryRes?.data)
                }
            }
        }
    }, [
        getEmployeeSalaryRes,
        getEmployeeSalaryError,
        getEmployeeSalaryPending
    ]);

    const handleCheckedUnCheckedAll = (e) => {
        const { type, checked } = e?.target
        setAllActiveSalaryCreate(checked)
        setCreatedSalaryEmpList((prevState) => ({
            ...prevState,
            data: prevState.data.map((item) => ({
                ...item,
                create_salary: checked,
            })),
        }));
    }

    // useEffect(() => {
    //     setCreatedSalaryEmpList((prevState) => ({
    //         ...prevState,
    //         data: prevState?.data?.map((item) => ({
    //             ...item,
    //             create_salary: allActiveSalaryCreate,
    //         })),
    //     }));
    // }, []);

    // useEffect(()=>{
    //     if(currentPage >1){
    //         setCreatedSalaryEmpList((prevState) => ({
    //             ...prevState,
    //             data: prevState?.data?.map((item) => ({
    //                 ...item,
    //                 create_salary: item?.create_salary ||allActiveSalaryCreate,
    //             })),
    //         })); 
    //     }
    // },[currentPage])
    const searchFilterData = (searchFiled, data) => {
        let tempCreatedSalaryEmpData = data
        if (Object.keys(data)?.length > 0) {
            let filterData = tempCreatedSalaryEmpData.filter((item, index) => item?.employee_name?.includes(searchFiled?.employee_name))
            setCreatedSalaryEmpList((prevState) => ({
                ...prevState,
                data: filterData,
            }));
        }
        else {
            setCreatedSalaryEmpList()
        }

    }


    /* Get All Departments */
    const getAllDepartments = () => {
        if (!getDepartmentListRes?.data?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(getDepartmentListRes?.data)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    /* Get All Employee List*/
    const getAllEmployee = () => {
        dispatch(employeeActions.getAllEmployeeRequest())
    }
    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code === 200) {

                    setAllEmployeeList(getAllEmployeeRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);

    const createPayRun = (payload) => {

    }
    const submitCreatePayRun = () => {
        const submitData = createdSalaryEmpList?.data?.filter((item) => item?.create_salary === true)
        const payload = {
            pay_run_employees: submitData
        }
        // dispatch(payrollActions?.addPayRunRequest(payload))
    }

    useEffect(() => {
        if (!addPayRunPending) {
            if (addPayRunError) {
                dispatch(payrollActions?.handleClearPayrollStore("HANDLE_CLEAR_ADD_PAY_RUN_STORE"))
                return;
            }
            if (addPayRunRes) {
                if (addPayRunRes?.code === 200) {
                    dispatch(payrollActions?.handleClearPayrollStore("HANDLE_CLEAR_ADD_PAY_RUN_STORE"))
                }
            }
        }
    }, [
        addPayRunRes,
        addPayRunError,
        addPayRunPending
    ]);



    return (
        <>
            <div
                className={`cardBox  creatNewTicketOff `}
            >
                <div className="header d-flex">
                    <Link to={"/pay-runs"} ><BackIcon /></Link>
                    <h5 className="title  fs-5 fw-bold text-primary">
                        Create Pay Run </h5>

                </div>
                <div className="body ">
                    <DynamicFilter
                        filters={employeeFilters}
                        handleFilterFormData={handleFilterFormData}
                        onSubmitHandleFilter={handleFilterEmployees}
                    />

                    <div className='mt-3'>
                        {/* <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Employees</h3> */}
                        <div className="row ms-2">
                            <CustCheckbox
                                key={"allActiveCreatedSalary"}
                                id={`allActiveCreatedSalary`}
                                checked={allActiveSalaryCreate}
                                handleChange={(e) => handleCheckedUnCheckedAll(e)}
                                name={"allActiveCreatedSalary"}
                                label={"Create Salary For All"}
                            />
                        </div>
                        <div style={createdSalaryEmpList?.data?.length > 9 ? { height: "539px", overflowX: "scroll" } : {}}>
                            <CustomTable
                                data={createdSalaryEmpList?.data}
                                fields={createPayRunFields}
                                isLoading={getEmployeeSalaryPending}
                                // pagination={true}
                                // currentPage={currentPage}
                                // setCurrentPage={setCurrentPage}
                                totalPages={createdSalaryEmpList?.totalPages}
                                // tableName={""}
                                // onSearch={handleSearch}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={createPayRunScopedSlots}
                            />
                        </div>
                    </div>

                </div>
                <div className="footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={"Submit"}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreatePayRun()}
                        // isLoading={selectedDepartmentData ? updateDepartmentPending : addDepartmentPending}
                        />
                    </div>
                </div>
            </div>
            {addPayRunPending && <CustomSpinner isShow={addPayRunPending} size="lg" />}
        </>
    )
}

export default CreatePayrun
